import React, { Component } from 'react';
import './Main.css';
import { Nav, NavItem, Navbar } from 'react-bootstrap';

class Main extends Component {
    render() {
        return (
            <div className='nav_wrapper'>
                <Navbar className="nav-wrapper" bsStyle="tabs" justified>
                    <Nav>
                        <NavItem className="nav-item" eventKey={1} href="/coursework/acceptance">
                            Приемка
    </NavItem>
                        <NavItem className="nav-item" eventKey={2} href="/coursework/warehouse">
                            Склад
    </NavItem>
                        <NavItem className="nav-item" eventKey={2} href="/coursework/shipment">
                            Отгрузка
    </NavItem>
                    </Nav>
                </Navbar>
            </div>
        );
    }
}

export default Main;


